.fixed-top {
  position: fixed;
  top: 75px;
  left: 0;
  width: 100%;
  z-index: 5;
  background-color: rgba(27, 214, 231, 0.993);
  text-align: center; }

@media screen and (max-width: 768px) {
  .nav-menu__burger {
    position: relative;
    height: 12px;
    transform: rotate(0);
    transition: 0.3s;
    background: none;
    visibility: visible; }
  .nav-menu {
    display: none; }
    .nav-menu__item {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0;
      padding: 0.5rem 0;
      width: 100%;
      background-color: rgba(27, 214, 231, 0.993); }
      .nav-menu__item:hover .nav-menu__link::after,
      .nav-menu__item--active .nav-menu__link::after {
        visibility: hidden; }
      .nav-menu__item:not(:last-child) {
        border-bottom: 1px solid rgba(255, 255, 255, 0.8); }
    .nav-menu__burger span {
      width: 100%;
      height: 2px;
      background: #fff;
      border-radius: 10px;
      position: absolute;
      left: 0;
      transform: rotate(0);
      transition: 0.3s; }
      .nav-menu__burger span:nth-child(1) {
        top: 0; }
      .nav-menu__burger span:nth-child(2), .nav-menu__burger span:nth-child(3) {
        top: 6px; }
      .nav-menu__burger span:nth-child(4) {
        top: 12px; }
    .nav-menu__burger.open span:nth-child(1), .nav-menu__burger.open span:nth-child(4) {
      width: 0;
      left: 50%;
      top: 6px; }
    .nav-menu__burger.open span:nth-child(2) {
      transform: rotate(45deg); }
    .nav-menu__burger.open span:nth-child(3) {
      transform: rotate(-45deg); } }
